@import url("https://fonts.googleapis.com/css2?family=Sunflower:wght@300&display=swap");
@import url("setTheme.css");

:root {
}

body {
  background-color: var(--main-bg-color-deepdark);

  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  /* color: var(--sub-text-color); */

  font-family: "Sunflower", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-weight: bold;
}

.gradient-background {
  background: radial-gradient(
    circle,
    var(--main-bg-color),
    var(--main-bg-color-deepdark)
  );
}

.App {
  width: 100vw;
  height: 100vh;

  text-align: center;

  display: flex;
  flex-direction: row;
  justify-content: center;
}

.section {
  padding: 75px 0px 0px 15px;
  display: flex;
  flex: 1;
}
.section0 {
  width: 100%;
}
.section1 {
  display: flex;
  flex-direction: column;
  padding-top: 25px;
}

.section2 {
  width: 400px;
  display: flex;
  align-items: start;
}

.section3 {
  display: flex;
  align-content: flex-start;
  align-items: flex-start;
  justify-content: flex-start;
}

#chartContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.chartBoxLarge {
  margin: 0 25px 0 25px;
}
.chartBox {
  margin: 10px;
}
.chartBox:hover {
  cursor: pointer;
  border-radius: 15px;
  box-shadow: 5px 3px 8px 4px rgb(4, 163, 158);
  /* solid 1px aquamarine; */
}

#userContainer {
  min-width: 80px;
  margin-top: 25px;
  padding-bottom: 15px;
  border-radius: 15px;
  /* background-color: var(--compo-bg-color); */
  background-color: var(--main-text-color-dark);
  color: var(--main-bg-color);
}
.userBlock {
  display: flex;
  justify-content: center;
  align-items: center;
  /* border-radius: 15px; */
  height: 50px;
  font-size: 0.9rem;
  /* margin: 20px 0; */
}
.userBlock:hover {
  cursor: pointer;
  color: var(--sub-text-color);
}

.profile-container {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
}
.profile-container:hover {
  cursor: pointer;
  color: var(--sub-text-color);
}

.profile-image {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  object-fit: cover;
  display: block;
  margin: 0 auto;
  margin-top: 10px;
  margin-bottom: 10px;
}
.profile-image:hover {
  outline: 2px solid var(--sub-text-color);
  cursor: pointer;
}

.profile-name {
  margin: 10px 0 20px 0;
  font-size: 1rem;
}

.smallScreen {
  display: none;
}
.leftSide {
  color: var(--main-text-color);

  border-radius: 15px;
  border-color: var(--main-text-color);
  border-width: 1px;
  border-style: solid;

  margin: 10px;
  padding: 20px;
  width: 300px;
}
.leftSideCompo0 {
  height: 100px;
}
.leftSideCompo1 {
  height: 150px;
}
.leftSideCompo2 {
  height: 120px;
}
.leftSideCompo3 {
  padding: 0px;
  width: 340px;
  /* height: 180px; */

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  border: none;
}
.leftSideCompo5 {
  height: 80px;
}
.leftSideCompo5:hover {
  /* background-color: var(--sub-text-color); */
  cursor: pointer;
}

.leftSideButton {
  margin: 5px 0;
  color: var(--main-bg-color);
  font-size: 4ex;
  border-radius: 12px;
  width: 105px;
  height: 85px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--main-text-color-dark);
}
.leftSideButton:hover {
  background-color: var(--sub-text-color);
  cursor: pointer;
}
.buttonCaption {
  display: block;
  margin: 5px 0px 0px 0px;
  font-size: 1.1rem;
}
.buttonCaption {
  color: var(--main-bg-color);
}

.leftSideCompo5 {
  font-size: 3.2rem;
  margin: 15px;
  margin-bottom: 20px;

  padding: 0px;
  width: 340px;
  /* height: 50px; */
  color: var(--sub-text-color);
  display: flex;
  /* justify-content: space-between; */
  /* flex-wrap: wrap; */

  border: none;
}

.main {
  width: 1250px;
  color: var(--main-text-color);
}

.clicked {
  background-color: var(--main-text-color);
}

.clicked2 {
  background-color: var(--sub-text-color);
}

/* 로그인 파트 */
#loginForm {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.login {
  font-size: 1.5rem;
}
.loginMsg {
  margin: 50px 0 30px 0;
}
.loginBox {
  margin: 22px 0 0 0;
  font-size: 2rem;
  height: 45px;
}
.loginInput {
  width: 180px;
  height: 40px;
  border: none;
  border-radius: 5px;
  font-size: 1.3rem;
  text-align: center;
}
.loginInput:focus {
  outline: none;
}
.loginButton {
  margin: 50px 0;
  font-size: 2.2rem;
  color: var(--main-bg-color);
  width: 180px;
  height: 75px;
  background-color: var(--main-text-color-dark);
  border-radius: 10px;

  display: grid;
  place-items: center;
}
.loginButton:hover {
  cursor: pointer;
  background-color: var(--sub-text-color);
}
.hidden {
  display: none;
}

/*  */
.button {
  margin: 50px 0;
  font-size: 2.2rem;
  color: var(--main-bg-color);
  width: 250px;
  height: 100px;
  background-color: var(--main-text-color-dark);
  border-radius: 10px;

  display: grid;
  place-items: center;
}
.button:hover {
  cursor: pointer;
  background-color: var(--sub-text-color);
}

@media screen and (min-width: 800px) and (max-width: 1299px) {
  .section2,
  .main {
    width: 450px !important;
  }
  #chartContainer {
    width: 450px !important;
  }
}
@media screen and (min-width: 1300px) and (max-width: 1799px) {
  .section2 {
    width: 850px !important;
  }
  #chartContainer {
    width: 850px !important;
  }
}
@media screen and (min-width: 1800px) {
  .section2 {
    width: 1250px !important;
  }
  #chartContainer {
    width: 1250px !important;
  }
}

/* 화면 크기가 500px 이하로 줄어들 때 */
@media (max-width: 790px) {
  .App {
    flex-direction: column;
    position: relative; /* 부모 컨테이너를 상대적으로 설정 */
  }
  .smallScreen {
    display: block;
  }
  .section1 {
    display: none;
  }
  .section3 {
    display: none;
  }
  .leftSideCompo0 {
    display: block;
  }
  .leftSideCompo3 {
    display: flex;
  }
  .leftSideCompo5 {
    height: 80px;
    display: block;
  }
  .section {
    padding: 25px 0px 0px 15px;
  }
  .section0 {
    flex: 0;
  }

  #chartContainer {
    position: absolute; /* 요소를 절대 위치로 설정 */

    /* top: 2; 상단에 위치 */
    left: 0;

    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100vw !important;
  }
}
